body {
  color: $body_black;
  font-family: "", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}

ul {
  list-style: none !important;
}

html {
  h1 { font-size: 28px; }
  h2 { font-size: 24px; }
  h3 { font-size: 20px; }
  h4 { font-size: 18px; }
  a {
    color: $black;
    text-decoration: none;
    &:hover {
      color: $black;
    }
  }
}

// font-size test 
html {
  h1 { font-size: 20px; }

  h2 { font-size: 18px; }

  h3 { font-size: 16px; }

  h4 { font-size: 15px; }
}

@include mq(md) {
  html {
    h1 { font-size: 18px; }
    h2 { font-size: 16px; }
    h3 { font-size: 15px; }
    h4 { font-size: 14px; }
  }
}
@include mq(lg) {
  html {
    h1 { font-size: 22px; }
    h2 { font-size: 20px; }
    h3 { font-size: 18px; }
    h4 { font-size: 16px; }
  }
}

